html {
  font-size: 16px;
  color: #000; }

body {
  font-family: 'Roboto', 'Arial', sans-serif;
  overflow-wrap: break-word;
  word-wrap: break-word; }

.container {
  min-width: 320px;
  max-width: 100%;
  padding: 0 0;
  box-sizing: border-box;
  margin: 0 auto; }

.wrap {
  min-width: 320px;
  max-width: 1140px;
  padding: 0 15px;
  box-sizing: border-box;
  margin: 0 auto; }

.header {
  background: url("/img/screen-bg.jpg") 35% top no-repeat #000;
  background-size: cover;
  color: #fff;
  position: relative;
  z-index: 1; }
  .header::before {
    background: linear-gradient(180deg, rgba(0, 0, 0, 0.5) 0%, rgba(255, 255, 255, 0) 48.62%, rgba(0, 0, 0, 0.5) 100%), rgba(24, 24, 24, 0.8);
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    z-index: -1; }

.header__wrap {
  padding-top: 5px; }

.header__wrap-two {
  padding-bottom: 30px; }

.header__title {
  padding-top: 2rem;
  line-height: 2.6875rem;
  color: #FCDB50;
  text-align: center;
  margin-top: 125px;
  margin-bottom: 24px;
  font-weight: 700; }

.header__subtitle {
  font-size: 1.5rem;
  line-height: 1.75rem;
  text-align: center;
  margin: 24px 0 64px 0;
  font-weight: 400; }

.header__buttons {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center; }

.header__work {
  width: 100%;
  background-color: #FCDB50;
  font-size: 0.875rem;
  line-height: 1rem;
  text-align: center;
  color: #2F2F2F;
  border-radius: 5px;
  cursor: pointer;
  border: none;
  display: block;
  padding-top: 14px;
  padding-bottom: 14px;
  text-decoration: none;
  transition-property: background-color;
  transition-duration: 1000ms;
  padding-top: 19px;
  padding-bottom: 19px;
  max-width: 288px; }
  .header__work:hover {
    background-color: #FFE67C; }

.header__lider {
  background: url("/img/rectangle.png") center no-repeat;
  background-size: 288px 54px;
  font-size: 0.875rem;
  line-height: 3.375rem;
  display: flex;
  justify-content: space-between;
  width: 288px;
  margin: 35px auto 95px;
  position: relative; }
  .header__lider::after {
    content: "";
    background: url("/img/medal.png");
    width: 82px;
    height: 83px;
    position: absolute;
    left: 130px;
    top: 50%;
    transform: translateY(-50%); }
  .header__lider span:nth-child(1) {
    margin-left: 30px; }
  .header__lider span:nth-child(2) {
    margin-right: 30px; }

.header__list {
  margin-bottom: 0;
  padding: 0; }

@media (min-resolution: 2dppx) {
  .header {
    background-image: url("/img/screen-bg-2x.jpg"); }
  .header__lider::after {
    background-image: url("/img/medal-2x.png");
    background-size: cover; } }

@media (min-width: 580px) {
  .header {
    background-position: center top; }
  .header__buttons {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    margin: 30px 0 95px; }
  .header__lider {
    flex-basis: 288px;
    flex-shrink: 0;
    margin: 0; }
  .header__work {
    flex-basis: 264px;
    max-width: 264px;
    margin-right: 23px; } }

@media (min-width: 768px) {
  .header__title {
    font-size: 2.75rem;
    line-height: 3.3125rem; }
  .header__subtitle {
    font-size: 1.75rem;
    line-height: 2rem; } }

@media (min-width: 1024px) {
  .header__title {
    font-size: 3.375rem;
    line-height: 3.9375rem;
    margin-top: 85px;
    margin-bottom: 12px; }
  .header__subtitle {
    font-size: 2rem;
    line-height: 2.3125rem;
    margin-top: 12px;
    margin-bottom: 30px; }
  .header__wrap {
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: calc(100% - 240px); } }

.fixed {
  position: fixed;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  z-index: 10;
  color: #fff;
  box-sizing: border-box;
  background-color: rgba(0, 0, 0, 0.8);
  width: 100%; }

.fixed__wrap {
  display: flex;
  align-items: center; }

.fixed__hamburger {
  flex-basis: 24px;
  flex-shrink: 0;
  cursor: pointer;
  margin-right: 11.3%; }
  .fixed__hamburger span {
    display: block;
    background-color: #FCDB50;
    height: 2px;
    border-radius: 4px;
    width: 100%;
    margin-bottom: 4px; }
    .fixed__hamburger span:nth-child(3) {
      margin-bottom: 0;
      width: 75%;
      margin-left: auto; }

.fixed__repair, .fixed__map {
  padding: 24px 0 24px 48px;
  background: url("../../img/repair.svg") left center no-repeat; }
  .fixed__repair p, .fixed__map p {
    margin: 0;
    font-size: 1.125rem;
    line-height: 1.3125rem; }
  .fixed__repair span, .fixed__map span {
    display: block;
    font-size: 0.875rem;
    line-height: 1rem; }

.fixed__phone {
  margin-left: auto; }
  .fixed__phone a {
    display: block;
    text-decoration: none;
    color: #fff; }
  .fixed__phone img {
    display: block; }
  .fixed__phone span {
    display: none;
    font-size: 1.5rem;
    line-height: 1.75rem; }

.fixed__map {
  display: none;
  background-image: url("/img/map.png"); }

.fixed__btn {
  width: 100%;
  background-color: #FCDB50;
  font-size: 0.875rem;
  line-height: 1rem;
  text-align: center;
  color: #2F2F2F;
  border-radius: 5px;
  cursor: pointer;
  border: none;
  display: block;
  padding-top: 14px;
  padding-bottom: 14px;
  text-decoration: none;
  transition-property: background-color;
  transition-duration: 1000ms;
  display: none;
  margin-left: 24px;
  max-width: 164px; }
  .fixed__btn:hover {
    background-color: #FFE67C; }

.fixed__callback {
  display: none;
  font-size: 0.875rem;
  line-height: 1.5rem;
  margin-left: 24px; }

@media (min-width: 530px) {
  .fixed__hamburger {
    margin-right: 3%; }
  .fixed__repair {
    margin-right: 4%; }
  .fixed__map {
    display: block; } }

@media (min-width: 700px) {
  .fixed__phone img {
    display: none; }
  .fixed__phone span {
    display: block; } }

@media (min-width: 930px) {
  .fixed__btn {
    display: block; } }

@media (min-width: 1090px) {
  .fixed__callback {
    display: block; }
  .fixed__hamburger {
    position: relative;
    margin-right: 7%; }
    .fixed__hamburger::after {
      content: "Меню";
      font-size: 1rem;
      line-height: 1rem;
      position: absolute;
      right: -50px;
      top: 50%;
      transform: translateY(-50%); } }

.advantages {
  list-style: none; }

.advantages__item {
  padding: 24px;
  box-sizing: border-box;
  background: rgba(0, 0, 0, 0.3);
  border-radius: 5px;
  text-align: center;
  margin-bottom: 24px; }
  .advantages__item:last-child {
    margin-bottom: 0; }

.advantages__text {
  font-size: 1.0625rem;
  line-height: 1.5rem;
  margin: 15px 0; }

@media (min-width: 1024px) {
  .advantages {
    display: flex;
    justify-content: center;
    max-width: 744px;
    margin: 0 auto;
    z-index: 1; }
    .advantages::after {
      position: absolute;
      content: "";
      background: rgba(0, 0, 0, 0.3);
      height: 145px;
      width: 88%;
      left: 0;
      bottom: 50px;
      z-index: -1;
      border-radius: 0 72px 72px 0; }
  .advantages__item {
    background: none;
    border-radius: 0;
    flex-basis: 25%;
    padding-left: 0;
    padding-right: 0;
    margin-bottom: 0; }
  .advantages__text {
    font-size: 0.875rem;
    line-height: 1.375rem; } }
